* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

/* HEADER */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 4rem;
  background-color: #fff;
}

.header-logo {
  height: 45px;
}

.nav-links a {
  margin-left: 2rem;
  text-decoration: none;
  color: #111;
  font-weight: 500;
}

.nav-links a:hover {
  color: #1a3d7c;
}

/* HERO */
.hero {
  text-align: center;
  margin-top: 3rem;
}

.hero h1 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: #111;
}

.upload-container {
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
}

.upload-box {
  background: #eef2f3;
  border-radius: 10px;
  width: 300px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.upload-label {
  background: #0c2340;
  color: #fff;
  border-radius: 25px;
  padding: 0.8rem 1.6rem;
  cursor: pointer;
  font-weight: 500;
  display: inline-block;
  transition: 0.3s ease;
}

.upload-label:hover {
  opacity: 0.9;
}

/* üñºÔ∏è Image preview inside box */
.preview-img {
  width: 80%;
  height: 80%;
  object-fit: contain;
  border-radius: 8px;
}

.cancel-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
}

.style-btn {
  margin-top: 2rem;
  background: #0c2340;
  color: #fff;
  border: none;
  border-radius: 30px;
  padding: 0.9rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s ease;
}

.style-btn:hover {
  background: #123a6d;
  transform: scale(1.05);
}

.result-container {
  margin-top: 2rem;
  text-align: center;
}

.result-img {
  width: 300px;
  border-radius: 10px;
  margin-bottom: 1rem;
}


/* HOW SECTION */
.how {
  background: #f5f5f5;
  text-align: left;
  padding: 6rem 5rem;
  margin-top: 3rem;
}

.how h2 {
  margin-bottom: 1rem;
}

.how ol {
  line-height: 1.8;
  font-size: 1rem;
}

/* ABOUT */
.about {
  text-align: center;
  padding: 9rem 5rem;
  font-size: 1.1rem;
  color: #333;
  
}

/* FOOTER */
.footer {
  background: #0c2340;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 3rem 5rem;
  flex-wrap: wrap;
  
}

.footer-logo {
  height: 60px;
}

.footer-right {

  text-align: right;
}

.footer-right h3 {
  margin-bottom: 1rem;
}

.social-icons {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-bottom: 1rem;
}

.social-icons img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.footer-right p {
  font-size: 0.9rem;
  margin-bottom: 0.3rem;
}

.footer-righ  {
  font-size: 0.8rem;
  text-align: center;
  color: #eef2f3;
  background: #0c2340;
  padding: 1rem;
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    padding: 1rem;
  }

  .footer {
    justify-content: center;
    
    text-align: center;
    
  }

  .footer-right {
    text-align: center;
    margin-top: 1.5rem;
  }

  .social-icons {
    justify-content: center;
  }
}
